}
body {
  background-image: linear-gradient(-90deg, rgba(33, 33, 33), rgb(43, 43, 43));
  color: aliceblue;
}

#side-by-side {
    display: inline-block;
    width: 10px;
    font-weight: 500;
  	font-family: Helvetica Neue;
}
#vert-menu > #side-by-side{
    position: relative;
    left: 20px;
    top: 20px;
    font-weight: 500;
  	font-family: Helvetica Neue;
}

#textBox {
    text-align: justify;
    text-justify: inter-word;
}

#picture {
  transform: translate(-50%, -50%);
  top: 52%;
  left: 50%;
  position: absolute;
  max-height:22cm;
  max-width:42cm;
  width: 100%;
  height: 100%;
}
path, circle {
  transition: all 500ms ease;
}

#picture {
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    position: absolute;
}
path, circle {
  transition: all 500ms ease;
}

